import { Motion, spring } from 'react-motion';
import SizeReporter from 'react-size-reporter';
import { withStyles } from '@material-ui/core/styles';
import { TextField } from '@material-ui/core';
import React, { Component } from 'react';
import style from './mapInfo.module.css';
import searchIcon from '../../../assets/img/icons/search.svg';
import arrow from '../../../assets/img/icons/arrow-full.svg';

const hour = time => {
  const date = new Date(Date.parse(time));
  return date;
};

const percentStyles = {
  position: 'absolute',
  zIndex: 2,
  top: 0,
  left: 0,
  height: '100%',
  background: '#0B8EC5',
};

const timePercent = (timeStart, timeEnd) => {
  const timeFull = Date.parse(timeEnd) - Date.parse(timeStart);
  const timeCollapsed = Date.now() - Date.parse(timeStart);

  return Math.round((timeCollapsed / timeFull) * 100) <= 100
    ? Math.round((timeCollapsed / timeFull) * 100)
    : 100;
};

function currentCrushChange(crushes, value) {
  const currentCrush = crushes.filter(crush =>
    crush.data.address.toLowerCase().includes(value.toLowerCase()),
  );
  return currentCrush;
}

const CssTextField = withStyles({
  root: {
    '&': {
      width: '100%',
    },
    '&:nth-child(2)': {
      marginBottom: 25,
    },
    '& .MuiInput-input': {
      paddingRight: 40,
      backgroundRepeat: 'no-repeat',
      color: '#ffffff',
    },
    '& label': {
      fontSize: 16,
      color: '#ffffff',
      textTransform: 'uppercase',
    },
    '& label.Mui-focused': {
      color: '#ffffff',
    },
    '&:hover .MuiInput-underline:before': {
      borderBottomColor: 'rgba(0, 0, 0, 0.42);',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#ffffff',
    },
    '& .MuiOutlinedInput-root': {
      '&.Mui-focused fieldset': {
        borderColor: '#ffffff',
      },
    },
  },
})(TextField);

export default class MapInfo extends Component {
  state = {
    searchValue: '',
    height: 0,
  };

  handleChange = ({ target }) => this.setState({ searchValue: target.value });

  handleSizeChange = ({ height }) => {
    this.setState({
      height,
    });
  };

  render() {
    const { currentId, dataMap, handleClick, search } = this.props;
    const { searchValue, height } = this.state;
    const currentCrushes =
      searchValue === '' ? dataMap : currentCrushChange(dataMap, searchValue);
    return (
      <div className={style.wrapper}>
        <form className={style.form}>
          <CssTextField
            className={style.input}
            onChange={this.handleChange}
            name="search"
            value={searchValue}
            type="text"
            id="map-input"
            label={search}
          />
          <img className={style.searchImg} alt="search icon" src={searchIcon} />
        </form>
        <ul className={style.list}>
          {currentCrushes.length > 0 ? (
            currentCrushes.map(crush => {
              return (
                <li onClick={() => handleClick(crush.id)} key={crush.id}>
                  <p className={style.streetName}>
                    <span>{crush.data.address}</span>
                    <img
                      className={
                        currentId && currentId === crush.id
                          ? style.arrowRotate
                          : style.arrow
                      }
                      src={arrow}
                      alt="arrow icon"
                    />
                  </p>
                  <p className={style.crushName}>{crush.data.name}</p>
                  <div className={style.dateWrapper}>
                    <p className={style.timeStart}>
                      <span className={style.hours}>
                        {hour(crush.data.start).getHours() >= 10
                          ? hour(crush.data.start).getHours()
                          : `0${hour(crush.data.start).getHours()}`}
                        :
                        {hour(crush.data.start).getMinutes() >= 10
                          ? hour(crush.data.start).getMinutes()
                          : `0${hour(crush.data.start).getMinutes()}`}
                      </span>
                      <span className={style.date}>
                        {hour(crush.data.start).getUTCDate() > 8
                          ? hour(crush.data.start).getUTCDate() + 1
                          : `0${hour(crush.data.start).getUTCDate() + 1}`}
                        :
                        {hour(crush.data.start).getUTCMonth() > 8
                          ? hour(crush.data.start).getUTCMonth() + 1
                          : `0${hour(crush.data.start).getUTCMonth() + 1}`}
                        :{hour(crush.data.start).getUTCFullYear()}
                      </span>
                    </p>
                    <p className={style.timeEnd}>
                      <span className={style.hours}>
                        {hour(crush.data.end).getHours() >= 10
                          ? hour(crush.data.end).getHours()
                          : `0${hour(crush.data.end).getHours()}`}
                        :
                        {hour(crush.data.end).getMinutes() >= 10
                          ? hour(crush.data.end).getMinutes()
                          : `0${hour(crush.data.end).getMinutes()}`}
                      </span>
                      <span className={style.date}>
                        {hour(crush.data.end).getUTCDate() > 8
                          ? hour(crush.data.end).getUTCDate() + 1
                          : `0${hour(crush.data.end).getUTCDate() + 1}`}
                        :
                        {hour(crush.data.end).getUTCMonth() > 8
                          ? hour(crush.data.end).getUTCMonth() + 1
                          : `0${hour(crush.data.end).getUTCMonth() + 1}`}
                        :{hour(crush.data.end).getUTCFullYear()}
                      </span>
                    </p>
                  </div>
                  <div className={style.timeDiagram}>
                    <span className={style.timePercent}>
                      {timePercent(crush.data.start, crush.data.end)}%
                    </span>
                    <div
                      style={{
                        ...percentStyles,
                        width: `${timePercent(
                          crush.data.start,
                          crush.data.end,
                        )}%`,
                      }}
                    />
                  </div>
                  <Motion
                    style={{
                      height: spring(
                        currentId && currentId === crush.id ? height : 0,
                      ),
                    }}
                  >
                    {interpolatedStyle => (
                      <div
                        className={style.listWrapper}
                        style={{
                          height: interpolatedStyle.height,
                        }}
                      >
                        <SizeReporter onSizeChange={this.handleSizeChange}>
                          <ul className={style.streetList}>
                            {crush.data.addresses.map((el, i) => (
                              <li className={style.streetListItem} key={i}>
                                {el}
                              </li>
                            ))}
                          </ul>
                          <div className={style.listMargin} />
                        </SizeReporter>
                      </div>
                    )}
                  </Motion>
                </li>
              );
            })
          ) : (
            <p className={style.noRezults}>No matching results!</p>
          )}
        </ul>
      </div>
    );
  }
}
