import React, { Fragment } from 'react';
import { Dialog, Slide, DialogContent, DialogTitle } from '@material-ui/core';
import Media from 'react-media';
import style from './modalMenu.module.css';
import styleMob from './modalMenuMob.module.css';
import MenuUk from './menuLang/menuUk';
import MenuRu from './menuLang/menuRu';
import MenuUkMob from './menuLangMob/menuUkMob';
import MenuRuMob from './menuLangMob/menuRuMob';
import './resetGlobals.css';

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="down" ref={ref} {...props} />;
});

const modalMenu = ({ handleClose, open, currentLocale }) => {
  return (
    <Dialog
      TransitionComponent={Transition}
      fullScreen
      onClose={handleClose}
      aria-labelledby="customized-dialog-title"
      open={open}
      className="modalMobile"
    >
      <Media query={{ minWidth: 1200 }}>
        {matches =>
          matches ? (
            <Fragment>
              <DialogTitle>
                <div className={style.wrapper}>
                  <h2 className={style.header}>
                    меню{' '}
                    <div className={style.close} onClick={handleClose}>
                      <div className={style.closeLine} />
                      <div className={style.closeLine} />
                    </div>
                  </h2>
                </div>
              </DialogTitle>
              <DialogContent>
                {currentLocale === 'ru' ? (
                  <MenuRu handleClose={handleClose} />
                ) : (
                  <MenuUk handleClose={handleClose} />
                )}
              </DialogContent>
            </Fragment>
          ) : (
            <Fragment>
              <DialogTitle>
                <div className="wrapper">
                  <div className={styleMob.close} onClick={handleClose}>
                    <div className={styleMob.closeLine} />
                    <div className={styleMob.closeLine} />
                  </div>
                  <h2 className={styleMob.header}>меню</h2>
                </div>
              </DialogTitle>
              <DialogContent>
                <div className="wrapper">
                  {currentLocale === 'ru' ? (
                    <MenuRuMob handleClose={handleClose} />
                  ) : (
                    <MenuUkMob handleClose={handleClose} />
                  )}
                </div>
              </DialogContent>
            </Fragment>
          )
        }
      </Media>
    </Dialog>
  );
};

export default modalMenu;
