import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import RegForm from './regForm/regForm';
import LoggedInfo from './loggedOffice/loggedOffice';
import * as sessionSelectors from '../../redux/session/sessionSelectors';
import * as sessionActions from '../../redux/session/sessionActions';
import * as sessionOperations from '../../redux/session/sessionOperations';
import style from './office.module.css';

class Office extends Component {
  state = {};

  componentDidMount() {
    this.props.refreshUser();
  }

  render() {
    const { autorization, authenticated, onLogout, user } = this.props;
    return (
      <div className={style.officeWrapper}>
        {!authenticated ? (
          <Fragment>
            <h2 className={style.loggingHeader}>{autorization.header}</h2>
            <RegForm {...autorization} />
          </Fragment>
        ) : (
          <LoggedInfo onLogout={onLogout} user={user} />
        )}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  authenticated: sessionSelectors.getIsAuthenticated(state),
  user: sessionSelectors.getUser(state),
});

const mapDispatchToProps = {
  onLogout: sessionOperations.logOutOperation,
  refreshUser: sessionOperations.refreshUser,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Office);
