import React from 'react';
import { NavLink } from 'react-router-dom';
import { ReactSvgInjector, Mutate } from 'react-svg-injector';
import Media from 'react-media';
import style from './officeNav.module.css';
import citizenryImg from '../../assets/img/icons/lk-citizenry.svg';
import enterpriseImg from '../../assets/img/icons/lk-enterprise.svg';
import pressImg from '../../assets/img/icons/lk-press.svg';

const officeNav = ({ citizenry, enterprise, press }) => (
  <div className={style.linkBackground}>
    <div className="wrapper">
      <div className={style.linkWrapper}>
        <NavLink
          className={style.link}
          activeClassName={style.linkSelected}
          to="/"
          exact
        >
          <Media query={{ maxWidth: 1199 }}>
            {matches =>
              matches ? (
                <ReactSvgInjector
                  src={citizenryImg}
                  alt="citizenry icon"
                  className={style.imgLink}
                >
                  <Mutate selector="g" />
                </ReactSvgInjector>
              ) : (
                <span>{citizenry}</span>
              )
            }
          </Media>
        </NavLink>
        <NavLink
          className={style.link}
          activeClassName={style.linkSelected}
          to="/enterprise"
        >
          <Media query={{ maxWidth: 1199 }}>
            {matches =>
              matches ? (
                <ReactSvgInjector
                  src={enterpriseImg}
                  alt="enterprise icon"
                  className={style.imgLink}
                >
                  <Mutate selector="g" />
                </ReactSvgInjector>
              ) : (
                <span>{enterprise}</span>
              )
            }
          </Media>
        </NavLink>
        <NavLink
          className={style.link}
          activeClassName={style.linkSelected}
          to="/press"
        >
          <Media query={{ maxWidth: 1199 }}>
            {matches =>
              matches ? (
                <ReactSvgInjector
                  src={pressImg}
                  alt="press icon"
                  className={style.imgLink}
                >
                  <Mutate selector="g" />
                </ReactSvgInjector>
              ) : (
                <span>{press}</span>
              )
            }
          </Media>
        </NavLink>
      </div>
    </div>
  </div>
);

export default officeNav;
