import { combineReducers } from 'redux';
import { ActionType } from './sessionActions';

const user = (state = null, { type, payload }) => {
  switch (type) {
    case ActionType.LOGIN_SUCCESS:
    case ActionType.REFRESH_CURENT_SUCCESS:
      return payload.response.user;

    case ActionType.LOGOUT:
      return null;

    default:
      return state;
  }
};

const authenticated = (state = false, { type }) => {
  switch (type) {
    case ActionType.LOGIN_SUCCESS:
    case ActionType.REFRESH_CURENT_SUCCESS:
      return true;

    case ActionType.LOGOUT:
      return false;

    default:
      return state;
  }
};

const token = (state = null, { type, payload }) => {
  switch (type) {
    case ActionType.LOGIN_SUCCESS:
      return payload.response.token;

    case ActionType.LOGOUT:
      return null;

    default:
      return state;
  }
};

const error = (state = null, { type, payload }) => {
  switch (type) {
    case ActionType.SIGNUP_ERROR:
    case ActionType.REFRESH_CURENT_ERROR:
      return payload.error;

    default:
      return state;
  }
};

const loading = (state = false, { type }) => {
  switch (type) {
    case ActionType.LOGIN_REQUEST:
    case ActionType.REFRESH_CURENT_REQUEST:
      return true;

    case ActionType.LOGIN_SUCCESS:
    case ActionType.LOGIN_ERROR:
    case ActionType.REFRESH_CURENT_SUCCESS:
    case ActionType.REFRESH_CURENT_ERROR:
      return false;

    default:
      return state;
  }
};

export default combineReducers({
  user,
  token,
  error,
  authenticated,
  loading,
});
