import React from 'react';
import { Switch, Route } from 'react-router-dom';
import style from './first100vh.module.css';
import Header from '../header/header';
import PageWithOffice from '../../pages/pageWithOffice/pageWithOffice';
import ErrorPage from '../../pages/errorPage/errorPage';

const FirstScreen = ({
  autorization,
  header,
  currentLocale,
  officeNavigation,
  quickMenu,
}) => (
  <div>
    <Switch>
      <Route
        exact
        path="/"
        render={() => (
          <PageWithOffice
            header={header}
            currentLocale={currentLocale}
            autorization={autorization.officeLK}
            officeNavigation={officeNavigation}
            quickMenu={quickMenu.citizenry}
          />
        )}
      />
      <Route
        path="/enterprise"
        render={() => (
          <PageWithOffice
            header={header}
            currentLocale={currentLocale}
            autorization={autorization.officeEnterprise}
            officeNavigation={officeNavigation}
            quickMenu={quickMenu.enterprise}
          />
        )}
      />
      <Route
        path="/press"
        render={() => (
          <PageWithOffice
            header={header}
            currentLocale={currentLocale}
            autorization={autorization.officePress}
            officeNavigation={officeNavigation}
            quickMenu={quickMenu.press}
          />
        )}
      />
      <Route render={() => <ErrorPage header={header} />} />
    </Switch>
  </div>
);

export default FirstScreen;
