import React, { Fragment } from 'react';
import { compose, withProps } from 'recompose';
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
} from 'react-google-maps';
import coldWater from '../../assets/img/icons/icon_cold water.svg';
import canalization from '../../assets/img/icons/icon_canalisation.svg';

const iconPath = type => {
  return type ? coldWater : canalization;
};

const MyMapComponent = compose(
  withProps({
    googleMapURL:
      'https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places',
    loadingElement: (
      <div
        style={{
          height: `100%`,
        }}
      />
    ),
    containerElement: (
      <div
        style={{
          height: `100%`,
        }}
      />
    ),
    mapElement: (
      <div
        style={{
          height: `100%`,
        }}
      />
    ),
  }),
  withScriptjs,
  withGoogleMap,
)(props => {
  const { currentLat, currentLng } = props;
  return (
    <GoogleMap
      defaultZoom={14}
      defaultCenter={{
        lat: 50.020965,
        lng: 36.229422,
      }}
      center={{
        lat: Number(currentLat),
        lng: Number(currentLng),
      }}
    >
      {props.markerList.map(marker => (
        <Marker
          key={marker.id}
          position={{
            lat: Number(marker.data.lat),
            lng: Number(marker.data.lng),
          }}
          icon={{
            url: iconPath(marker.data.type),
            scaledSize: new window.google.maps.Size(50, 50),
          }}
          onClick={() => props.handleClick(marker.id)}
        />
      ))}
    </GoogleMap>
  );
});

