import React, { Component, Fragment } from 'react';
import { style } from '@material-ui/system';
import Localization from '../../context/localization';
import FirstScreen from '../../components/first100vh/first100vh';
import MapControl from '../../components/mapControl/mapControl';
import styles from './startPage.module.css';

class StartPage extends Component {
  state = {};

  render() {
    const {
      autorization,
      header,
      currentLocale,
      officeNavigation,
      quickMenu,
      map,
    } = this.props;
    return (
      <Fragment>
        <FirstScreen
          autorization={autorization}
          header={header}
          currentLocale={currentLocale}
          officeNavigation={officeNavigation}
          quickMenu={quickMenu}
        />
        <div className={styles.contentBlock}>
          <MapControl {...map} currentLocale={currentLocale} />
        </div>
      </Fragment>
    );
  }
}

export default function LocalizedStartPage() {
  return (
    <Localization.Consumer>
      {({ localizedContent, currentLocale }) => (
        <StartPage {...localizedContent} currentLocale={currentLocale} />
      )}
    </Localization.Consumer>
  );
}
