import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withStyles } from '@material-ui/core/styles';
import { TextField } from '@material-ui/core';
import * as sessionOperations from '../../../redux/session/sessionOperations';
import emailSVG from '../../../assets/img/icons/e-mail.svg';
import passwordSVG from '../../../assets/img/icons/password.svg';
import style from './regForm.module.css';

const CssTextField = withStyles({
  root: {
    '&:nth-child(2)': {
      marginBottom: 25,
    },
    '& .MuiInput-input': {
      paddingRight: 40,
      backgroundRepeat: 'no-repeat',
    },
    '&:nth-child(1) .MuiInput-input': {
      backgroundImage: `url(${emailSVG})`,
      backgroundSize: '30px 20px',
      backgroundPosition: '100% center',
    },
    '&:nth-child(2) .MuiInput-input': {
      backgroundImage: `url(${passwordSVG})`,
      backgroundSize: '30px 28px',
      backgroundPosition: '100% 0%',
    },
    '& label': {
      fontSize: 16,
      color: '#1E3139',
    },
    '& label.Mui-focused': {
      color: '#0B8EC5',
    },
    '&:hover .MuiInput-underline:before': {
      borderBottomColor: 'rgba(0, 0, 0, 0.42);',
    },
    '& .MuiInput-underline:after': {
      borderBottomColor: '#0B8EC5',
    },
    '& .MuiOutlinedInput-root': {
      '&.Mui-focused fieldset': {
        borderColor: '#0B8EC5',
      },
    },
  },
})(TextField);

class RegForm extends Component {
  state = {
    mail: '',
    password: '',
    remmember: false,
  };

  handleChange = ({ target }) => {
    const { name, value } = target;

    this.setState({ [name]: value });
  };

  handleToggle = ({ target }) => {
    const { checked } = target;
    this.setState({ remmember: checked });
  };

  handleSubmit = e => {
    e.preventDefault();

    this.props.onLogin({ ...this.state });
  };

  render() {
    const { mail, password, remmember } = this.state;
    const {
      enterLogin,
      enterPassword,
      remmemberMe,
      forgot,
      enter,
      register,
      demo,
    } = this.props;

    return (
      <form onSubmit={this.handleSubmit} className={style.form}>
        <CssTextField
          onChange={this.handleChange}
          name="mail"
          value={mail}
          type="email"
          id="standard-input"
          label={enterLogin}
        />
        <CssTextField
          onChange={this.handleChange}
          name="password"
          value={password}
          type="password"
          id="standard-password"
          label={enterPassword}
        />
        <div className={style.controlWrapper}>
          <label className={style.checkBoxLabel}>
            <input
              onChange={this.handleToggle}
              checked={remmember}
              type="checkbox"
            />
            <span>{remmemberMe}</span>
          </label>
          <a
            className={style.forgotPassword}
            href="https://cpp.infoxvod.com.ua/"
            target="_blank"
            rel="noopener noreferrer"
          >
            {forgot}
          </a>
        </div>
        <button className={style.button} type="submit">
          {enter}
        </button>
        <div className={style.buttonWrapper}>
          <a
            href="https://cpp.infoxvod.com.ua/"
            target="_blank"
            rel="noopener noreferrer"
            className={style.linkButton}
          >
            {register}
          </a>
          <a
            href="https://cpp.infoxvod.com.ua/#DEMO"
            target="_blank"
            rel="noopener noreferrer"
            className={style.linkButton}
          >
            {demo}
          </a>
        </div>
      </form>
    );
  }
}

const mapDispatchToProps = {
  onLogin: sessionOperations.login,
};

export default connect(
  null,
  mapDispatchToProps,
)(RegForm);
