import React, { Component } from 'react';
import addresList from '../../bd/googleMapMarkers.json';
import GoogleMap from '../googleMap/googleMap';
import MapInfo from './mapInfo/mapInfo';
import style from './mapControl.module.css';

export default class MapControl extends Component {
  state = {
    currentId: null,
    currentLat: '50.020965',
    currentLng: '36.229422',
  };

  setActiveId = currentId => {
    const currentCoordNew = addresList.crashesUK.dataMap.find(
      el => el.id === currentId,
    ).data;
    this.setState({
      currentId,
      currentLat: currentCoordNew.lat,
      currentLng: currentCoordNew.lng,
    });
  };

  handleClick = currentId => {
    if (currentId === this.state.currentId) {
      this.setState({ currentId: null });
    } else {
      this.setActiveId(currentId);
    }
  };

  handleClose = () => this.setState({ currentId: null });

  render() {
    const { currentId, currentLat, currentLng } = this.state;
    const { currentLocale, mapHeader, search } = this.props;
    const dataLang =
      currentLocale === 'uk' ? addresList.crashesUK : addresList.crashesRU;

    return (
      <div className={style.wrapper}>
        <h2 className={style.header}>{mapHeader}</h2>
        <div className={style.mapControlWrapper}>
          <div className={style.mapInfoWrapper}>
            <MapInfo
              handleClick={this.handleClick}
              {...dataLang}
              currentId={currentId}
              search={search}
            />
          </div>
          <div className={style.mapWrapper}>
            <GoogleMap
              markerList={dataLang.dataMap}
              handleClick={this.handleClick}
              currentLat={currentLat}
              currentLng={currentLng}
            />
          </div>
        </div>
      </div>
    );
  }
}
