import React from 'react';
import { Link } from 'react-router-dom';
import style from './menuListDesk.module.css';

const MenuListDesk = ({ list = [] }) => (
  <ul className={style.list}>
    {list.map(item =>
      item.in === 'true' ? (
        <li className={style.listItem} key={item.title}>
          <Link className={style.link} to={item.address}>
            {item.title}
          </Link>
        </li>
      ) : (
        <li key={item.title} className={style.listItem}>
          <a
            className={style.link}
            href={item.address}
            rel="noopener noreferrer"
            target="_blank"
          >
            {item.title}
          </a>
        </li>
      ),
    )}
  </ul>
);

export default MenuListDesk;
