import React, { Component } from 'react';
import { Motion, spring } from 'react-motion';
import SizeReporter from 'react-size-reporter';
import { ReactSvgInjector, Mutate } from 'react-svg-injector';
import { Link } from 'react-router-dom';
import style from './logoMob.module.css';
import logo from '../../../assets/img/logo/logo_mob.png';
import logoW from '../../../assets/img/logo/logo_mob.webp';
import logoJ from '../../../assets/img/logo/logo_mob.jp2';
import LocalSelector from '../../localSelector/localeSelector';
import icon24 from '../../../assets/img/icons/24-hours-support.svg';
import ModalMenu from '../../modalMenu/modalMenu';
import tel from '../../../assets/img/icons/tel.svg';
import kyivstar from '../../../assets/img/icons/kyivstar.svg';
import life from '../../../assets/img/icons/life.svg';
import vodafone from '../../../assets/img/icons/vodafone.svg';

const color = openTels => (openTels ? '#0B8EC5' : '#929C9F');

export default class LogoMob extends Component {
  state = {
    open: false,
    openTels: false,
    height: 0,
  };

  onOpenModal = () => {
    this.setState({ open: true });
  };

  onCloseModal = () => {
    this.setState({ open: false });
  };

  onOpenModalTels = () => {
    this.setState(prevState => ({ openTels: !prevState.openTels }));
  };

  handleSizeChange = ({ height }) => {
    this.setState({
      height,
    });
  };

  render() {
    const { title, currentLocale } = this.props;
    const { open, openTels, height } = this.state;
    return (
      <div className={style.background}>
        <div className="wrapper">
          <div className={style.wrapper}>
            <div className={style.left}>
              <div onClick={this.onOpenModal} className={style.burger}>
                <div className={style.burgerLine} />
                <div className={style.burgerLine} />
                <div className={style.burgerLine} />
                <div className={style.burgerLine} />
              </div>
              <Link to="/">
                <picture>
                  <source srcSet={logoW} type="image/webp" />
                  <source srcSet={logoJ} type="image/jp2" />
                  <source srcSet={logo} type="image/png" />
                  <img className={style.img} src={logo} alt="infox logo" />
                </picture>
              </Link>
            </div>
            <div className={style.right}>
              <span
                className={style.spanWrapper}
                onClick={this.onOpenModalTels}
              >
                <ReactSvgInjector
                  src={icon24}
                  alt="icon support"
                  className={style.imgSupport}
                >
                  <Mutate selector="g" fill={color(openTels)} />
                </ReactSvgInjector>
              </span>
              <LocalSelector />
            </div>
          </div>
          <Motion style={{ height: spring(!openTels ? 0 : height) }}>
            {interpolatedStyle => (
              <div
                className={style.heightWrapper}
                style={{
                  height: interpolatedStyle.height,
                }}
              >
                <SizeReporter onSizeChange={this.handleSizeChange}>
                  <div className={style.tels}>
                    <h3 className={style.telsTitle}>{title}</h3>
                    <div className={style.telWrapper}>
                      <img src={tel} alt="tel logo" className={style.telLogo} />
                      <span className={style.telText}>
                        <a className={style.tel} href="tel:+380487055505">
                          705-55-05
                        </a>
                        ,&nbsp;
                        <a className={style.tel} href="tel:+380487805505">
                          780-55-05
                        </a>
                      </span>
                    </div>
                    <div className={style.telWrapper}>
                      <img
                        src={kyivstar}
                        alt="kyivstar logo"
                        className={style.telLogo}
                      />
                      <span className={style.telText}>
                        <a className={style.tel} href="tel:+380689055505">
                          +38 (068) 905-55-05
                        </a>
                      </span>
                    </div>
                    <div className={style.telWrapper}>
                      <img
                        src={vodafone}
                        alt="vodafone logo"
                        className={style.telLogo}
                      />
                      <span className={style.telText}>
                        <a className={style.tel} href="tel:+380669055505">
                          +38 (066) 905-55-05
                        </a>
                      </span>
                    </div>
                    <div className={style.telWrapper}>
                      <img
                        src={life}
                        alt="life logo"
                        className={style.telLogo}
                      />
                      <span className={style.telText}>
                        <a className={style.tel} href="tel:+380931700155">
                          +38 (093) 170-01-55
                        </a>
                      </span>
                    </div>
                  </div>
                </SizeReporter>
              </div>
            )}
          </Motion>
          <ModalMenu
            handleClose={this.onCloseModal}
            open={open}
            currentLocale={currentLocale}
          />
        </div>
      </div>
    );
  }
}
