import React, { Component } from 'react';
import { LocalizationContext } from '../../context/localization';
import styleDesktop from './localSelectorDesktop.module.css';
import styleMobile from './localSelectorMobile.module.css';

const getBtnClass = (btnLocale, currentLocale, desktop) => {
  if (desktop)
    return btnLocale === currentLocale
      ? styleDesktop.active
      : styleDesktop.button;
  return btnLocale === currentLocale ? styleMobile.active : styleMobile.button;
};

export default class LocaleSelector extends Component {
  static contextType = LocalizationContext;

  render() {
    const { currentLocale, locales, changeLocale } = this.context;
    const { desktop } = this.props;
    return (
      <section
        className={desktop ? styleDesktop.selector : styleMobile.selector}
      >
        {locales.map(locale => (
          <button
            key={locale}
            onClick={() => changeLocale(locale)}
            className={getBtnClass(locale, currentLocale, desktop)}
          >
            {locale === 'uk' ? 'УКР' : 'РУС'}
          </button>
        ))}
      </section>
    );
  }
}
