import React from 'react';
import Media from 'react-media';
import LocaleSelector from '../localSelector/localeSelector';
import TelsDesk from './telsDesk/telsDesk';
import style from './header.module.css';
import LogoDesk from './LogoDesk/logoDesk';
import LogoMob from './logoMob/logoMob';

const Header = ({ title, currentLocale }) => {
  return (
    <header>
      <Media query={{ minWidth: 1200 }}>
        {matches => matches && <TelsDesk title={title} />}
      </Media>
      <Media query={{ minWidth: 1200 }}>
        {matches =>
          matches ? (
            <LogoDesk currentLocale={currentLocale} />
          ) : (
            <LogoMob title={title} currentLocale={currentLocale} />
          )
        }
      </Media>
    </header>
  );
};

export default Header;
